import React, { Component } from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { startClock, changeName, addCount, serverRenderClock } from '../store'


const NameTag = () => {
  return (
    <div>
       FROM NAMETAG
    </div>
  )
}



//Retrieve state to use in render:
const mapStateToProps = (state) => {
  console.log('MaStToProps: ', state)
  return {
    name: state.name
  }
}


const mapDispatchToProps = dispatch => {
  return {
    // Retrieves action in store, and makes accressible as "addCount"
    changeName: bindActionCreators(changeName, dispatch)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NameTag)